      <li><a href="tree.html" class="iconfont icon-icon"></a></li>
      <li><a href="perfume.html" class="iconfont icon-icon1"></a></li>
    </ul>
  </div>
</template>

<script>
import promise from 'es6-promise'
promise.polyfill();
import Vue from 'vue'

new Vue({
  el: '#app',
})
export default {
  name: 'App',
  data() {
    return {

    }
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import "../assets/iconfont/iconfont.css";
// @import "../../assets/iconfont/iconfont.ttf";
#app {
  width: 100%;
  margin: 0 auto;
}
#nav{
  position: fixed;
  z-index: 99999;
  left: 20px;
  width: 60px;
  background-color: aquamarine;
}
ul,li{
  list-style: none;
}
a{
  text-decoration: none;
}
.iconfont{
  width: 40px;
  text-align: center;
  display: block;

  border: 1px solid #ccc;
  font: 600 30px '微软雅黑';
}
</style>



