<template>
    <div id="app">
        <div class="title">
            THOSE YEARS, The Days We've Been Through Together <span>>></span>
        </div>
        <div id="container">
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
            <div class="item"><img src="../../assets/img/1.jpg" alt=""></div>
        </div>
        <nav-to></nav-to>
    </div>
</template>

<script>
import navTo from '@/components/navTo'
export default {
    components: {
        navTo
    },
     mounted(){
        $('#container').waterfall({
            itemCls: '.item',
            colWidth: 240,
            spacingWidth: 20,
            spacingHeight: 10,
            // gutterWidth: 15,
            // gutterHeight: 20,
            minCol: 2, 
            checkImagesLoaded: false,
            isAnimated: true,
            isFadeIn: true,
            animationOptions: {
            }
        })
    },
}
</script>

<style media="screen" lang="less">
@import "../../assets/css/reset.css";
@import "../../assets/css/waterfall.css";

.title{
    text-align: center;
    color: #000;
    text-shadow: 4px 2px 2px #ddd; 
    font: 700 30px '微软雅黑';
    background-color: #999;
    padding: 10px;
    border: 6px solid #ccc;
    position: relative;
    span{
        display: block;
        position: absolute;
        top: 8px;
        left: 100px;
        animation: arrow 1s ease-in-out infinite;
    }
    @keyframes arrow{
        0%{transform: translateX(100px)} 
        // 25%{transform: translateX(120px)}
        50%{transform: translateX(160px)}
        // 75%{transform: translateX(160px)} 
        100%{transform: translateX(100px)}
        // 50%{transform: translateX(50px)}
    }

}
#container {
    margin: 10px auto;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    .item {
        float: left;
        padding: 10px;
        margin: 10px;
        border: 1px solid #ccc;
        background: #f7f7f7;
        box-shadow: 10px 10px 8px #ccc; 
        overflow: hidden;
        &:hover {
            box-shadow: 16px 16px 20px #999;   
        }     
        img {
            width: 240px;
            transition: all .6s;
        }
        &:hover img{
            transition: all 1s;  
            transform: scale(1.2);
        }   
    }         
}
</style>



